* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}
body {
  text-align: center;
}

input {
  margin-top: 60px;
  width: 600px;
  height: 50px;
  padding: 0 10px 0 10px;

  background-color: ffffff;
  border: 1px solid #cfc3c3;
}

button {
  width: 80px;
  height: 50px;
  margin-bottom: 50px;

  background-color: rgb(166, 144, 238);
  border: none;
  cursor: pointer;
}

button:disabled {
  background-color: #9d1cc5;
}

button:hover,
button:focus {
  background-color: #9d1cc5;
  border: 2px solid #112ae6;
}

.timer {
  display: flex;
  justify-content: center;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100px;
  padding: 10px 0 10px 0;
  margin-right: 20px;
  border: 1px solid #100f0f5c;
  border-radius: 40px;
}

.field:last-child {
  margin-right: 0;
}

.value {
  font-weight: 700;
  font-size: 60px;
}

.label {
  color: #191818;

}